export default {
	components: {},
	destroyed() {

	},
	data() {
		return {
			mawbfocus: true,
			mawb: '',

			bigfocus: false,
			big: "",

			ErrorRemind: "",
			OutWareHouseNumber: 0,
			BagCount: 0,
			bigdisabled: "disabled"
		}
	},
	onLoad() {
		this.mawbfocus = true;
		this.mawb = '';

		this.bigfocus = false;
		this.big = '';

		this.ErrorRemind = '';
		this.OutWareHouseNumber = 0;
		this.BagCount = 0;
		this.bigdisabled = "disabled";
	},
	onShow() {
		this.bigfocus = false;
		this.big = '';

		this.ErrorRemind = '';
		this.OutWareHouseNumber = 0;
		this.BagCount = 0;
		this.bigdisabled = "disabled";
	},
	methods: {
		focus_mawb() {
			if (this.mawb.length > 0) {
				this.mawb = '';
				this.BagCount = 0;
			}
			setTimeout(() => {
				uni.hideKeyboard();
			}, 500);
		},
		focus_big() {
			setTimeout(() => {
				uni.hideKeyboard();
			}, 500);
		},
		//校验主单是否输出
		mawb_confirm() {
			console.log('该主单：' + this.mawb);
			var mawb = this.mawb.trim();
			if (mawb.length < 1) {
				this.bigdisabled = "disabled";
				return;
			}
			var user = uni.getStorageSync('userName');
			var token = uni.getStorageSync('token');
			var Url = this.$websiteUrl + '/api/mmawb/GetMawbBagOutboundCount?mawb=' + mawb + '&user=' + user + '&token=' +
				token;
			uni.request({
				url: Url,
				method: 'GET',
				//data: {username:User_Value,password:Pass_Value},
				header: {
					'content-type': 'application/x-www-form-urlencoded' //自定义请求头信息
				},
				success: res => {
					console.log('请求回执:' + JSON.stringify(res.data));
					if (res.data.code == '999') {
						uni.navigateTo({
							url: '/pages/zaizai-login/index'
						});
					}
					if (res.data.code == '200') {
						// if(res.data.result == null)
						// {
						// 	res.data.result = 2000;
						// }
						this.mawbfocus = false;
						this.bigfocus = false;

						this.ErrorRemind = '';
						this.BagCount = res.data.result == null ? 0 : res.data.result;

						if (this.mawb.length > 0) {
							this.bigdisabled = false;
						}
					} else {
						this.ErrorRemind = res.data.text;
						this.mawb = '';
						this.BagCount = 0;
					}
				},
				fail: failres => {
					this.ErrorRemind = "请求异常";
					return;
				},
				complete: () => {}
			});
		},
		//验证大包
		big_confirm() {
			var big = this.big.trim();
			var mawb = this.mawb.trim();
			console.log(big);
			if (mawb.length < 1) {
				return;
			}

			var userName = uni.getStorageSync('userName');
			var token = uni.getStorageSync('token');
			var Url = this.$websiteUrl + '/api/moutbound/post';
			uni.request({
				url: Url,
				method: 'POST',
				data: {
					mawb: mawb,
					bagid: big,
					user: userName,
					token: token
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded' //自定义请求头信息
				},
				success: res => {
					console.log('请求回执:' + JSON.stringify(res.data));
					if (res.data.code == '999') {
						uni.navigateTo({
							url: '/pages/zaizai-login/index'
						});
					}
					if (res.data.code == '200') {
						this.mawbfocus = false;
						this.bigfocus = true;
						this.ErrorRemind = '';
						this.OutWareHouseNumber = res.data.result;

						if (this.mawb.length > 0) {
							this.bigdisabled = false;
						}
					} else {
						this.ErrorRemind = res.data.text;
					}

					this.big = '';
				},
				fail: failres => {
					this.ErrorRemind = "请求异常";
					return;
				},
				complete: () => {}
			});
		},
		mawb_blur() {
			if (this.mawb.length > 0) {
				this.bigdisabled = false;
			} else {
				this.bigdisabled = 'disabled';
			}
		},

	}
}
